// Root colors
:root {
    --primary-white-background: #F5F5F7;
    --primary-black-background: #000000;

    --primary-white-Text-color: #D0D0D0;
    --primary-black-Text-color: #1D1D1F;

    --primary-good-alert-color: rgb(30, 215, 96);
    --primary-error-alert-color: rgb(255, 0, 0);

    --primary-white-button-background: rgba(208, 208, 208, 0.55);
}

.brands-inside-section {
    max-width: 600px;
    height: auto;
    margin-top: 705px;
    background-color: rgb(0, 0, 0);
    margin-bottom: 10px;
    padding: 23px 0;
    overflow: hidden;

    .brands-inside-text-head {
        text-align: center;
        padding-bottom: 23px;
        h1 {
            font-style: normal;
            font-weight: 400;
            font-size: 29px;
            color: var(--primary-white-Text-color);
        }
    }

    .brands-inside-names {
        img{
            &:nth-child(2){
                margin-top:17px;
                padding-top: 19px;
                border-top: 1px solid var(--primary-white-Text-color);
            }
            &:nth-child(3){
                margin-top: 17px;
                padding-top: 19px;
                border-top: 1px solid var(--primary-white-Text-color);
            }
        }
    }
}


@media only screen and (min-width: 572px) {
    .brands-inside-section {
        height: auto;
        margin-top: 485px;
    }
}